import './scss/main.scss';

import { fetchAvailability, fetchRooms } from './js/api.js';
import {
  initCalendar,
  getSelectedDates,
  updateCalendarData,
  resetCalendarSelection,
} from './js/calendar.js';
import { displayRooms } from './js/room-display.js';
import 'flatpickr/dist/flatpickr.min.css';

document.addEventListener('DOMContentLoaded', () => {
  const checkAvailabilityBtn = document.getElementById('check-availability-btn');
  const closeModalBtn = document.getElementById('close-calendar-modal-btn');
  const calendarModal = document.getElementById('calendar-modal');
  const checkRoomsBtn = document.getElementById('check-rooms-btn');
  const resetButton = document.getElementById('reset-calendar-and-results-btn');
  const roomsContainer = document.getElementById('rooms-container'); // Kontejner pro vsledky pokoj
  const selectedDatesInfo = document.getElementById('selected-dates-info'); // Info o vybranch datech
  const loader = document.getElementById('loader');

  const showLoader = () => {
    if (loader) loader.classList.add('show');
  };

  const hideLoader = () => {
    if (loader) loader.classList.remove('show');
  };

  // Zpracovn tlatka "Zkontrolovat dostupnost"
  checkAvailabilityBtn.addEventListener('click', async () => {
    const adults = document.getElementById('adults').value;
    const children = document.getElementById('children').value;
    const dateStart = document.getElementById('date-start').value;
    const dateEnd = document.getElementById('date-end').value;

    const response = await fetchAvailability({ adults, children, dateStart, dateEnd });
    const availabilities = response._embedded.hotel_availabilities;

    updateCalendarData(availabilities);
    initCalendar();

    calendarModal.classList.add('show');

    closeModalBtn.addEventListener('click', () => {
      calendarModal.classList.remove('show');
      resetCalendarSelection();
    });
  });

  // Zpracovn tlatka "Zobrazit pokoje"
  checkRoomsBtn.addEventListener('click', async () => {
    const { startDate, endDate } = getSelectedDates();

    if (startDate && endDate) {
      const adults = document.getElementById('adults').value;
      const children = document.getElementById('children').value;

      calendarModal.classList.remove('show');
      showLoader();

      try {
        const roomsData = await fetchRooms({ startDate, endDate, adults, children });

        // Po naten dat schovat loader a zobrazit vsledky
        displayRooms(roomsData);
      } catch (error) {
        console.error('Chyba pi natn pokoj:', error);
        roomsContainer.innerHTML = '<p>Chyba pi natn vsledk. Zkuste to prosm znovu.</p>';
      } finally {
        // Schovat loader, a u byl poadavek spn nebo ne
        hideLoader();
      }
    } else {
      // Pokud nen vybrno sprvn datum
      roomsContainer.innerHTML = '<p>Vyberte prosm platn rozsah dat.</p>';
    }
  });

  // Zpracovn tlatka "Reset vbru a vsledk"
  resetButton.addEventListener('click', () => {
    console.log('Resetting calendar and results');

    // Reset kalende
    if (typeof resetCalendarSelection === 'function') {
      resetCalendarSelection();
    }

    // Reset zobrazench vsledk pokoj
    if (roomsContainer) {
      roomsContainer.innerHTML =
        '<p>Vsledky byly resetovny. Vyberte nov datum a dostupnost.</p>';
    }

    // Reset informanho prvku o vybranch datech
    if (selectedDatesInfo) {
      selectedDatesInfo.textContent = 'Vbr byl resetovn. Vyberte nov datum.';
    }
  });
});
